*{
  font-family: 'Ubuntu'!important;
}
body{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  flex:1;
}
.btn-primary{
  background-color: #ff7e00;
  border-color: #ff7e00;
  font-weight: 700;
  border-radius: 0;
}
.btn-primary:hover{
  background:#FBA81A;
  transition: all .5s;
  border-color: rgba(251, 160, 50, 1);
}
.wrapper{
  max-width: 1120px;
  width:80%;
  margin: 0 auto;
}
h2{
  font-size: 36px;
}
p.enlarge{
  font-size:20px;
}
section{
  padding:25px 0px;
}
section h3{
  border-bottom: 2px solid #bfbfbf;
  padding-bottom:15px;
  font-size:32px;
  margin-bottom: 25px;
}
#logoContainer{
  flex:1;
}
nav ul.nav{
  display: flex;
  justify-content: center;
}
header{
  display: flex;
  left:0;
  right:0;
  flex-direction: column;
}
.social{
  align-items: center;
  display: flex;
  height:40px;
  justify-content: flex-end;
  flex:1;
}
.social span{
  color: #333;
  font-size:18px;
}
.social a{
  padding:0px 10px;
}
.social span:hover{
  color: #FBA81A;
  transition: all .5s;

}
nav{
  display: flex;
}
nav .wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:10px 0px;
  position: relative;
}
ul.navigation{
  list-style: none;
  display: flex;
  flex:1;
  justify-content: space-between;
  height:100%;
  margin-top: 10px;
}
nav ul.navigation li.top-level a.top{
  font-size: 14px;
  font-weight: 700;
  color: #333;
  align-self: center;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}
nav ul.navigation li.top-level a.top:hover{
  text-decoration: none;
  color: #222222;
  transition: color .5s;
  transition: background .5s;
  position: relative;
  top: 2px;
  border-bottom:3px solid #ff7e00;
}
.active{
  /*background: rgba(0, 0, 0, .25);*/
  text-decoration: none;
  color: #FF7E00;
  /*transition: all .5s;*/
  border-bottom:3px solid #ff7e00;
  top:2px;
  position: relative;
}
nav ul.navigation li.top-level{
  align-items: center;
  align-self: center;
  justify-content: center;
  display: flex;
  vertical-align: middle;
  flex: 1;
  position: relative;
}
.dropdown-icon{
  padding-left:10px;
}
ul.dropdown{
  position: absolute;
  bottom:-260px;
  left:0;
  background: rgba(0, 0, 0, .9);
  padding-left:0;
  flex:2;
  width:300px;
  display: flex;
  flex-direction: column;
  list-style: none;
  align-items: flex-start;
  padding-left:30px;
  padding-top:20px;
  z-index:1;
  display: block;
}
ul.dropdown:hover{
  display: block!important;
}
ul.dropdown li{
  height: 40px;
}
ul.dropdown li a{
  font-size:14px!important;
  text-decoration: none;
  color: #f9f9f9;
  height: 40px;
}
ul.dropdown li a:hover{
  color: #FF7E00;
  border:none!important;
  text-decoration: none!important;
  height:40px;

}
#hero{
 align-items: center;
 display: flex;
 justify-content: center;
 padding-top:40px;
 margin-top:20px;
}
#hero .headshot{
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex:1;
}
#hero .headshot img{
  border-radius: 100%;
  flex: 1;
  max-height: 280px;
  align-self: center;
  max-width: 280px;
  height: auto;
}
#hero p{
  font-size: 48px;
  line-height: 54px;
  margin-top: 20px;
  text-align: center;
}
#hero p.hello{
  font-size:170px;
  font-weight: 900;
  letter-spacing: -10px;
  margin-top:-100px;
  text-shadow: 9px -9px 0 #ffffff;
  line-height: 170px;
}
/* About Me Section */
section#about{
  max-width: 80%;
  margin: 0 auto;
}
section#about *{
  text-align:left;
}
section#about p{
  font-size:18px;
  /*margin: 20px 0px;*/
}
.specialty-logos{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding:20px 0px;
}
.specialty-logos div{
  max-width: 20%;
  flex:1;
  align-items: center;
}
.specialty-logos div img{
  max-width: 100%;
  height
  auto;
}
/* Recent Posts Section */
#recentPosts{
  display: flex;
  flex-direction: column;
  flex:1;
}
#recentPosts .singlePost{
  display: flex;
  flex-direction: row;
  box-shadow: 2px 2px 0px #bfbfbf;
  margin-bottom: 40px;
}
#recentPosts .singlePost:nth-child(odd){
  flex-direction: row-reverse;
}
#recentPosts .featured-image{
  flex:1;
}
#recentPosts .featured-image img{
  max-width: 100%;
}
#recentPosts .post{
  flex:2;
  display: flex;
  flex-direction: column;
  padding:25px;
  background: #f4f4f4;
}
#recentPosts .post h1{
  text-transform: uppercase;
  font-weight: 900;
}
#recentPosts span.date{
  font-size:12px;
  margin-bottom: 20px;
}
#recentPosts span.singleCategory{
  background: #ff7e00;
  padding:5px 10px;
  border-radius: 3px;
  margin:0px 10px;
  color: #fff;
}
#recentPosts span.singleCategory:hover{
  background: #FBA81A;
  transition: all .5s;
}
#recentPosts span.singleCategory:first-child{
  margin-left:0!important;
}
#recentPosts p.postExcerpt a.read-more{
  color: #ff7e00;
  padding-left:10px;
  text-decoration: none;
  transition: all .5s;
  font-weight: 900;
}
#recentPosts p.postExcerpt a.read-more:hover{
  color: #fBA81a;
  font-weight: 900;
}
#recentPosts p.postExcerpt a.read-more:hover i{
  padding-left:10px;
  color: #fBA81a;
  transition: all .5s;

}
#recentPosts p.postExcerpt a.read-more i{
  color: #ff7e00;
  font-size:10px;
  position: relative;
  bottom: 1px;
  padding-left:5px;
}

#subscribe{
  display: flex;
  justify-content: center;
}
#subscribe #mc_embed_signup{
  background:#fff;
  clear:left;
  width: 40%;
}
#mc_embed_signup .button{
  background:#ff7e00;
  font-weight: 700;
}
#mc_embed_signup .button:hover{
  background: #FBA81A;
}
#mc_embed_signup_scroll{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
#mc_embed_signup input.email{
  width: auto;
}
/**
 * Single Post Page
 */
 a.singleCategory{
   background: #ff7e00;
   padding:5px 10px;
   border-radius: 3px;
   margin:0px 10px;
   color: #fff;
   font-size:12px;
 }
 a.singleCategory:hover{
   background: #FBA81A;
   transition: all .5s;
   text-decoration: none;
 }
 a.singleCategory:first-child{
   margin-left:0!important;
 }

 /**
  * Project Page
  */
ul.portfolio{
  display:flex;
  flex-direction:row;
  list-style: none;
  justify-content: space-between;
  padding:25px 0px;
}
ul.portfolio li{
  max-width: 20%;
  display: flex;
  position: relative;
}
ul.portfolio li img{
  max-width: 100%;
}
ul.portfolio li div.img-container{
  flex:1;
}
ul.portfolio li div.overlay .btn{

}
ul.portfolio div.overlay{
  position: absolute;
  left: 0;
  top:0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  display: flex;
  align-items: center;
  justify-content: center;
}
.software-row{
  display: flex;
  flex-direction: row;
  align-items:center;
  padding:25px 10px;
  box-shadow: 2px 2px 0px #bfbfbf;
  background: #ebebeb;
  margin:25px 0px;
}
.software-row img{
  max-width: 25%;
  height: auto;
}
.software-row .description{
  max-width:75%;
}
.software-row .description p{
  padding:0px 35px;
}

/**
 * Contact Page
 */

#contactForm{
  width:75%;
}
textarea:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.uneditable-input:focus {
  border-color: rgba(255, 126, 0, 0.4)!important;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(255, 126, 0, 0.4)!important;
  outline: 0 none!important;
}
#contactForm textarea{
  height:200px;
}

div.response{
  width:75%;
  padding:15px;
}
div.response p{
  color: #f9f9f9;
  margin-bottom:0;
}
div.response.error{
  background:rgba(225, 40, 25, .75);
}
div.response.success{
  background:rgba(40, 160, 60, .75);
}
.btn-primary i{
  margin-left:15px;
}

/**
* Sidebar
*/
#main{
  display: flex;
  flex-direction: row;
}
.content{
  flex:3;
  margin-top: 25px;
  padding-right:25px;
}
.sidebar{
  flex:1;
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 25%;
}
.sidebar img{
  border-radius: 100%;
  flex: 1;
  max-height: 180px;
  align-self: center;
  max-width: 180px;
  height: auto;
  margin:25px 0px;
}
.sidebar .follow-social{
  display: flex;
  flex-direction: column;
}
.follow-social a, .follow-social div{
  margin: 15px 0px;;
}

.sidebar .newsletter-signup{
  padding:30px 0px;
}
.newsletter-sidebar{
  display: flex;
}
.sidebar .newsletter-signup p{
  text-align: center;
}



/**
 * Footer
 */
footer{
  padding:20px 0px;
  flex: 1;
  align-items: flex-end;
  display: flex;
}
footer .wrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex:1;
}
footer p{
  margin:0;
  font-size: 12px;
}
footer .copyright{
  display: flex;
  align-items: center;
}
footer .social a span{
  font-size:14px;
}
